.wrapper,
html,
body {
  height: 100%;
  margin: 0;
}


.wrapper {
  display: flex;
  height:75px;
  background-color: #C4E2FF;
  padding-top: 0px;
  padding-left: 0px;
}

.left {
  flex: 0 0 65%;
}

.right {
  flex: 1;
}

.items{
  display:flex;
  padding-top:30px;
  font-size: 17px;
  font-family: sohne, "Helvetica Neue", Helvetica, Arial, sans-serif;

}

